.about_container {
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-rows: 33rem;
    grid-template-areas:
      'a b b b';
}
.sidebar {
    grid-area: a;
}
.about_box {
    grid-area: b;
}

.tech_stacks {
    display: flex;
    justify-content: center;
}